import React, { useEffect, useState } from "react";
import App from './App'
import { createPortal } from "react-dom";
import FastSpin from "./Spin/Spin";


const Kungfu = ()=>{

    const over_all = document.querySelector('body');


    const [a, b] = useState(false);
    const lebanon = createPortal(<div> <App /> </div>, document.querySelector('.no_index_important_skip_bot'));

    useEffect(()=>{
    over_all.style.backgroundImage = `url(data:image/jpeg;base64,)`
        b(false);
        setTimeout(() => {
            b(true)
            over_all.style.backgroundImage = `url(data:image/png;base64,)`
        }, 4500);
    }, [over_all.style]);

    return(<>
        { !a ? <FastSpin /> : lebanon }
    </>)
};

export default Kungfu;